.message {
    max-width: 300px;
    margin-right: 0;
    margin-left: 25px;
    margin-bottom: 40px;
    margin-left: 25px;
    margin-right: 0;
    &.me {
        margin-left: auto;
        margin-right: 25px;
    }
    .plan {
    background: #eeedf8;
    padding: 15px 24px;
    border-radius: 20px;
    &::before {
        content: "";
        position: absolute;
        z-index: 0;
        bottom: 0;
        left: -8px;
        height: 20px;
        width: 20px;
        background: inherit;
        border-bottom-right-radius: 15px;
    }
    &::after{
        content: "";
        position: absolute;
        z-index: 1;
        bottom: 0;
        left: -10px;
        width: 10px;
        height: 20px;
        background: white;
        border-bottom-right-radius: 15px;
    } &.me {
        &::before {
            content: "";
            position: absolute;
            z-index: 0;
            bottom: 0;
            right: -8px;
            height: 20px;
            width: 20px;
            background: #eeedf8;
            border-bottom-left-radius: 15px;
        }
        &::after{
            content: "";
            position: absolute;
            z-index: 1;
            bottom: 0;
            right: -10px;
            width: 10px;
            height: 20px;
            background: white;
            border-bottom-left-radius: 15px;
        }
    }
    &:not(.me) {
        &::before {
            content: "";
            position: absolute;
            z-index: 0;
            bottom: 0;
            left: -8px;
            height: 20px;
            width: 20px;
            background: inherit;
            border-bottom-right-radius: 15px;
        }
        &::after{
            content: "";
            position: absolute;
            z-index: 1;
            bottom: 0;
            left: -10px;
            width: 10px;
            height: 20px;
            background: white;
            border-bottom-left-radius: 15px;
        }
    }
    .headline {
        font-size: 14px;
        color: #546566;
        margin-bottom: 11px;
    }
    .date {
        display: flex;
        align-items: flex-start;
        font-size: 12px;
        color: #546566;
        img {
            width: 12px;
            margin-right: 10px;
        }
        .content {
            p {
                margin: 0;
                &.details {
                    font-size: 10px;
                    margin: 2px 0 10px 0;
                }
            }
        }
    }
    .info {
        display: flex;
        justify-content: space-between;
        .status , .history {
            display: flex;
            align-items: center;
            font-size: 10px;

            img {
                width: 15px;
                margin-right: 12px;
            }
        }
        .status {
            p {
                color: #ce5b4c ;
            }
        }
        .history {
            p {
                color: #546566;
            }
        }
    }
}
}
